(function() {
'use strict';

  angular.module('app.notification', [])
	.service("appNotify", ['ngNotify', appNotify ])
	.service("appToastNotify", appToastNotify);

  
  /**
     * appNotify service.
     *
     * Show notification
     *
     * @inject ngNotify
     *
     * @return object
     *-------------------------------------------------------- */
   
  function appNotify( ngNotify ) {


      /*
       Notification Default Option Object
      -------------------------------------------------------------------------- */
      
      this.optionsObj = {
        position      : 'botttom',
        type          : 'success',
        theme         : 'pure',
        dismissQueue  : true,
        duration      : 3000,
        sticky        : false
      };

      /**
        * Show success notification message
        *
        * @param string - message
        * @param object - options  
        *
        * @return object
        *---------------------------------------------------------------- */

      this.success  =  function( message, options ) {

          if ( _.isEmpty( options ) ) {  // Check for if options empty
              var options = {};
          }

          options.type = 'success';

          this.notify( message, options );

      };

        /**
          * Show error notification message
          *
          * @param string - message
          * @param object - options 
          *
          * @return object
          *---------------------------------------------------------------- */

        this.error  =  function( message, options ) {

            if ( _.isEmpty( options ) ) {  // Check for if options empty
                var options = {};
            }

            options.type = 'error';

            this.notify( message, options );

        };

        /**
          * Show information notification message
          *
          * @param string - message
          * @param object - options  
          *
          * @return object
          *---------------------------------------------------------------- */

        this.info  =  function( message, options ) {

            if ( _.isEmpty( options ) ) {  // Check for if options empty
                var options = {};
            }

            options.type = 'info';

            this.notify( message, options );

        };

        /**
          * Show warning notification message
          *
          * @param string - message
          * @param object - options  
          *
          * @return object
          *---------------------------------------------------------------- */

        this.warn  =  function( message, options ) {

            if ( _.isEmpty( options ) ) {  // Check for if options empty
                  var options = {};
            }

            options.type = 'warn';

            this.notify( message, options );

        };

        /**
          * Show notification
          *
          * @param string msg
          * @param object options
          *
          * @return void
          *---------------------------------------------------------------- */

        this.notify = function( message, options ) {
          
            // show notification
            ngNotify.set( message, _.assign( this.optionsObj, options ) );

        };
      
  };

	/**
     * appNotify service.
     *
     * Show notification
     *
     * @return object
     *-------------------------------------------------------- */
   
  	function appToastNotify() {


		/*
		Notification Default Option Object
		-------------------------------------------------------------------------- */

		this.optionsObj = {
			styling: 'fontawesome',
			width  : 'auto',
			desktop:true,
			hide: false,
			icon : false,
			history: {
        		history: false
    		},
			buttons: {
		        closer  : false,
		        sticker : false
		    },
			animate: {
		        animate   : true,
		        in_class  : 'fadeInRight',
		        out_class : 'fadeOutRight'
		    }
		};

		/**
	    * Show success notification message
	    *
	    * @param string - message
	    * @param object - options  
	    *
	    * @return object
	    *---------------------------------------------------------------- */

	  	this.success  =  function( message, options ) {

			if ( _.isEmpty( options ) ) {  // Check for if options empty
                var options = {};
            }

            options.type = 'success';

			if (!_.isObject(message)) {

				_.assign( this.optionsObj, {
					text : message
				});

			} else if(_.isObject(message)) {

				_.assign( this.optionsObj, message );
			}

			var notice = new PNotify( _.assign( this.optionsObj, options ));

			notice.get().click(function() {
			    notice.remove();
			});

	  	};

		/**
	    * Show success notification message
	    *
	    * @param string - message
	    * @param object - options  
	    *
	    * @return object
	    *---------------------------------------------------------------- */

	  	this.error  =  function( message, options ) {

			if ( _.isEmpty( options ) ) {  // Check for if options empty
                var options = {};
            }

            options.type = 'danger';

			if (!_.isObject(message)) {

				_.assign( this.optionsObj, {
					text : message
				});

			} else if(_.isObject(message)) {

				_.assign( this.optionsObj, message );
			}

			var notice = new PNotify( _.assign( this.optionsObj, options ));

			notice.get().click(function() {
			    notice.remove();
			});

	  	};

		/**
	    * Show success notification message
	    *
	    * @param string - message
	    * @param object - options  
	    *
	    * @return object
	    *---------------------------------------------------------------- */

	  	this.warn  =  function( message, options ) {

			if ( _.isEmpty( options ) ) {  // Check for if options empty
                var options = {};
            }

            options.type = 'warning';

			if (!_.isObject(message)) {

				_.assign( this.optionsObj, {
					text : message
				});

			} else if(_.isObject(message)) {

				_.assign( this.optionsObj, message );
			}

			var notice = new PNotify( _.assign( this.optionsObj, options ));

			notice.get().click(function() {
			    notice.remove();
			});

	  	};


		/**
	    * Show success notification message
	    *
	    * @param string - message
	    * @param object - options  
	    *
	    * @return object
	    *---------------------------------------------------------------- */

	  	this.info  =  function( message, options ) {

			if ( _.isEmpty( options ) ) {  // Check for if options empty
                var options = {};
            }

            options.type = 'info';

			if (!_.isObject(message)) {

				_.assign( this.optionsObj, {
					text : message
				});

			} else if(_.isObject(message)) {

				_.assign( this.optionsObj, message );
			}

			var notice = new PNotify( _.assign( this.optionsObj, options ));

			notice.get().click(function() {
			    notice.remove();
			});
	  	};
      
  	};

})();